import React from 'react';
import { Accordion, AccordionItem, AccordionButton, AccordionPanel, AccordionIcon, Box, Text, Heading, VStack } from '@chakra-ui/react';
import { Link } from "react-router-dom";
import { Link as ALink } from '@chakra-ui/react';

const EpAccordion = ({eps}) => {
    console.log("Eps: " + {eps})
    return (
        
        <Accordion allowToggle>
            <>
            {eps.sort((a, b) => a.fields.episode_id > b.fields.episode_id ? 1 : -1)
                .map((ep, i) => {
                return (
                    <AccordionItem key={i}>
                        <AccordionButton>
                            <Box flex="1" textAlign="left">
                                <Heading as="h2" size="md">{eps[i].fields.title}</Heading>
                            </Box>
                            <AccordionIcon />
                        </AccordionButton>
                        <AccordionPanel pb={4}>
                            <VStack spacing={4}>
                                <Text>{eps[i].fields.description}</Text>
                                <Text align="left" 
                                    color="teal.700"
                                    _hover={{
                                        background: "teal.700",
                                        color: "white",
                                    }}>
                                <Link to={`/episode/${eps[i].id}`}>Read all about {eps[i].fields.title}</Link></Text>
                            </VStack>
                        </AccordionPanel>
                    </AccordionItem>
                    );
                })
            }
            </>
        </Accordion>
    )
}

export default EpAccordion;