import React, { Component, useState, useEffect } from 'react';
import { useParams } from 'react-router-dom'
import { Box, Text, Grid, GridItem } from '@chakra-ui/react';
import EpisodeCard from './EpisodeCard';
import EpisodeGuests from './EpisodeGuests';

const api_key = process.env.REACT_APP_AIRTABLE_API_KEY

function isEmpty(obj) {
  return Object.keys(obj).length === 0;
}

function EpisodePage() {
  let params = useParams()
  const [ep_data, setData] = useState({})
  const [guest_data, setGuestData] = useState({})

  const fetchEpisodeData = async () => {
    fetch(`https://api.airtable.com/v0/apptQdtHUtoeXaw0s/episodes/${params.id}?api_key=${api_key}`)
			.then(ep_data => ep_data.json())
			.then(ep_data => {
        setData(ep_data)
			})
			.catch(error => console.log(error))
    }

  const fetchGuestData = async () => {
    fetch(`https://api.airtable.com/v0/apptQdtHUtoeXaw0s/guests?api_key=${api_key}&filterByFormula=%7Bepisode_id%5B0%5D%7D+%3D+'recr2wbbxiVuSLDwH'`)
      .then(guest_data => guest_data.json())
      .then(guest_data => {
        setGuestData(guest_data)
        console.log(guest_data)
      })
      .catch(error => console.log(error))
  }

  useEffect(() => {
    fetchEpisodeData();
    fetchGuestData()
  }, [])

  // console.log(data)

  return (
    <Grid templateColumns="3fr 6fr 3fr" gap={3}>
      <GridItem colStart={2} colEnd={3}>
        <Box m={12}>
          {/* <Text>Episode page</Text> */}
          {isEmpty(ep_data) ? <Text>Loading...</Text> : <EpisodeCard data={ep_data}/>}
        </Box>  
      </GridItem>
      <GridItem colStart={2} colEnd={3}>
        <Box m={12}>
          <EpisodeGuests></EpisodeGuests>
        </Box>
      </GridItem>
    </Grid>

  )
}

export default EpisodePage;