import React, { Component, useState, useEffect } from 'react';
import { BrowserRouter, Route, Switch, Link, useParams } from 'react-router-dom';
import { ChakraProvider, Box, Text, Grid, theme, GridItem, Heading } from '@chakra-ui/react';
import Header from './Header';
import Footer from './Footer';
import Home from './Home';
import canvasoffwhite from './images/canvasoffwhite.jpg';
import EpisodePage from './EpisodePage';

const api_key = process.env.REACT_APP_AIRTABLE_API_KEY

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
                  eps: []
                  };
    // console.log(this.state)
  }

  componentDidMount() {
        fetch(`https://api.airtable.com/v0/apptQdtHUtoeXaw0s/Episodes?api_key=${api_key}`)
            .then(res => res.json())
            .then(res => {
                // console.log(res.records)
                this.setState({ eps: res.records })
        // console.log(this.state)
            })
            .catch(error => console.log(error))
      
    };

  render() {
    return (
    <ChakraProvider theme={theme}>
    <Box sx={{backgroundImage:{canvasoffwhite}, backgroundRepeat:"repeat"}} bg="yellow.50">
      <Grid templateColumns="repeat(12, 1fr)" gap={3}>
        <GridItem colSpan={12}>
          <Header></Header>
        </GridItem>
      
        <GridItem colSpan={12}>
          <Box pt={2} pb={2} bg="blue.100" boxShadow="lg">
            <Box p={4} 
                  textAlign="center" 
                  fontSize="l" 
                  bg="blue.100" 
                  border="3px" 
                  borderColor="gray.200" 
                  borderTopStyle="dashed"
                  borderBottomStyle="dashed">
              <Heading as="h3" size="md">Top travel tips for time travellers</Heading>
            </Box>
          </Box>
        </GridItem>
      
        <GridItem colSpan={12}>
          <Switch>
            <Route path="/" render={(props) => (
                <Home {...props} eps={this.state.eps} />
                )} exact />
            {/* <Route path="/episode/:id" component={EpisodePage} /> */}
            <Route path="/episode/:id" render={(props) => (
                <EpisodePage {...props}  />
                )} exact />
            {/* <Route component={Error} /> */}
          </Switch>
        </GridItem>

        <GridItem colSpan={12}>
          <Footer></Footer>
        </GridItem>
      </Grid>
    </Box>
    </ChakraProvider>
    )
}
}


export default App;
